* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Segoe UI", Arial, sans-serif;
  line-height: 1.4;
  color: #444;
  background: #fff;
  height: 100vh;
  padding: 10px;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}

.inner {
  width: 100%;
  padding: 8rem 10px 4rem;
}

.form-container {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  border-radius: 50px;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
  justify-content: space-evenly;
}

.input-text {
  font-size: 1rem;
  font-weight: 400;
  width: 85%;
  padding-right: 5px;
  padding-left: 10px;
  border-radius: 50px;
}

.input-text,
.input-submit {
  height: 45px;
  outline: none;
  border: none;
}

.input-text::-moz-placeholder {
  color: rgb(0, 0, 0);
}

.input-text:-ms-input-placeholder {
  color: rgb(0, 0, 0);
}

.input-text::placeholder {
  color: rgb(0, 0, 0);
}

.input-submit {
  background: transparent;
  color: #5b5b5b;
  text-transform: capitalize;
  cursor: pointer;
  font-weight: 600;
  margin-right: 10px;
}

.input-submit svg {
  color: #008b8b;
  font-size: 20px;
  margin-top: 2px;
}

.faTrash {
  color: #8b0808;
  font-size: 16px;
}/*# sourceMappingURL=App.css.map */